"use client"

import { useSession, signOut } from "next-auth/react"
import { useQuery } from "@tanstack/react-query"
import { 
  ArrowLeft, 
  User, 
  Phone, 
  MapPin, 
  Milk, 
  LogOut,
  ChevronRight,
  Package,
  Wallet
} from "lucide-react"
import { useRouter } from "next/navigation"
import { Card, Avatar, Badge, SectionLoader, Button } from "@/components/ui"
import { formatPaise, formatQuantity, CUSTOMER_STATUS_LABELS } from "@/lib/constants"
import { formatPhone } from "@/lib/utils"

export default function ProfilePage() {
  const { data: session } = useSession()
  const router = useRouter()

  const { data, isLoading } = useQuery({
    queryKey: ["customer-profile"],
    queryFn: async () => {
      const res = await fetch("/api/customer/profile")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const handleSignOut = async () => {
    await signOut({ callbackUrl: "/" })
  }

  if (isLoading) return <SectionLoader />

  const customer = data?.data?.customer
  const subscription = customer?.subscription
  const wallet = customer?.wallet
  const bottleBalance = data?.data?.bottleBalance

  return (
    <div className="p-4">
      {/* Header */}
      <div className="flex items-center gap-4 mb-6">
        <button
          onClick={() => router.back()}
          className="h-10 w-10 flex items-center justify-center rounded-xl bg-neutral-100"
        >
          <ArrowLeft className="h-5 w-5" />
        </button>
        <h1 className="text-xl font-bold text-neutral-900">Profile</h1>
      </div>

      {/* Profile Card */}
      <Card className="mb-4">
        <div className="flex items-center gap-4">
          <Avatar
            src={customer?.profileImage}
            fallback={customer?.name}
            size="xl"
          />
          <div className="flex-1">
            <h2 className="font-semibold text-lg text-neutral-900">{customer?.name}</h2>
            <p className="text-sm text-neutral-500">{customer?.email}</p>
            <Badge
              variant={
                customer?.status === "ACTIVE" ? "success" :
                customer?.status === "BLOCKED" ? "error" :
                "warning"
              }
              className="mt-2"
            >
              {CUSTOMER_STATUS_LABELS[customer?.status as keyof typeof CUSTOMER_STATUS_LABELS]}
            </Badge>
          </div>
        </div>
      </Card>

      {/* Contact Info */}
      <Card className="mb-4">
        <h3 className="font-semibold text-neutral-900 mb-4">Contact Information</h3>
        
        <div className="space-y-4">
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 bg-neutral-100 rounded-xl flex items-center justify-center">
              <Phone className="h-5 w-5 text-neutral-600" />
            </div>
            <div>
              <p className="text-sm text-neutral-500">Phone</p>
              <p className="font-medium text-neutral-900">{formatPhone(customer?.phone)}</p>
            </div>
          </div>

          <div className="flex items-start gap-3">
            <div className="h-10 w-10 bg-neutral-100 rounded-xl flex items-center justify-center shrink-0">
              <MapPin className="h-5 w-5 text-neutral-600" />
            </div>
            <div>
              <p className="text-sm text-neutral-500">Delivery Address</p>
              <p className="font-medium text-neutral-900">
                {customer?.addressLine1}
                {customer?.addressLine2 && `, ${customer.addressLine2}`}
              </p>
              {customer?.landmark && (
                <p className="text-sm text-neutral-500">Near {customer.landmark}</p>
              )}
              <p className="text-sm text-neutral-500">
                {customer?.city} - {customer?.pincode}
              </p>
            </div>
          </div>
        </div>
      </Card>

      {/* Subscription Info */}
      {subscription && (
        <Card className="mb-4">
          <h3 className="font-semibold text-neutral-900 mb-4">Subscription</h3>
          
          <div className="space-y-3">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="h-10 w-10 bg-primary-100 rounded-xl flex items-center justify-center">
                  <Milk className="h-5 w-5 text-primary-600" />
                </div>
                <div>
                  <p className="text-sm text-neutral-500">Daily Quantity</p>
                  <p className="font-medium text-neutral-900">
                    {formatQuantity(subscription.dailyQuantityMl)}
                  </p>
                </div>
              </div>
              <span className="text-sm text-neutral-500">
                {formatPaise(subscription.dailyPricePaise)}/day
              </span>
            </div>

            <div className="flex items-center justify-between pt-3 border-t border-neutral-100">
              <span className="text-sm text-neutral-500">Current Cycle</span>
              <span className="font-medium text-neutral-900">
                Month {subscription.paymentCycleCount}
              </span>
            </div>

            <div className="flex items-center justify-between">
              <span className="text-sm text-neutral-500">Next Deposit Due</span>
              <span className="font-medium text-neutral-900">
                Month {Math.ceil(subscription.paymentCycleCount / 3) * 3 + 1}
              </span>
            </div>
          </div>
        </Card>
      )}

      {/* Quick Stats */}
      <div className="grid grid-cols-2 gap-3 mb-4">
        <Card>
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 bg-green-100 rounded-xl flex items-center justify-center">
              <Wallet className="h-5 w-5 text-green-600" />
            </div>
            <div>
              <p className="text-xs text-neutral-500">Wallet</p>
              <p className={`font-bold ${(wallet?.balancePaise || 0) < 0 ? 'text-red-600' : 'text-neutral-900'}`}>
                {formatPaise(wallet?.balancePaise || 0)}
              </p>
            </div>
          </div>
        </Card>

        <Card>
          <div className="flex items-center gap-3">
            <div className="h-10 w-10 bg-amber-100 rounded-xl flex items-center justify-center">
              <Package className="h-5 w-5 text-amber-600" />
            </div>
            <div>
              <p className="text-xs text-neutral-500">Bottles</p>
              <p className="font-bold text-neutral-900">
                {(bottleBalance?.largeBottles || 0) + (bottleBalance?.smallBottles || 0)}
              </p>
            </div>
          </div>
        </Card>
      </div>

      {/* Sign Out */}
      <Button
        variant="ghost"
        fullWidth
        onClick={handleSignOut}
        className="text-red-600 hover:bg-red-50"
      >
        <LogOut className="h-5 w-5" />
        Sign Out
      </Button>
    </div>
  )
}
